.ContactForm {
  &__error {
    padding-top: 5px;
    min-height: 30px;
    font-size: 14px;
    margin: 0;

    &_textarea {
      position: absolute;
      bottom: -40px;
      left: 0;
    }
  }

  &__form {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    text-align: left;
  }

  &__content {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    text-align: left;
  }

  &__container {
    display: flex;
  }

  &__inputs {
    display: flex;
    flex-direction: column;
    margin: 0 30px 35px 0;

    &:last-of-type {
      margin: 0 0 35px;
    }
  }

  &__label {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    display: flex;
  }

  &__input {
    max-width: 255px;
    width: 100%;
    padding: 15px 0;

    box-sizing: border-box;
    font-weight: 300;
    font-size: 21px;
    line-height: 29px;

    color: #bdbdbd;
    border: none;
    border-bottom: 1px solid #bdbdbd;
    background-color: transparent;

    outline: none;

    &:focus {
      outline: none;
      border-bottom: 1px solid #005fa3;
    }

    &_invalid {
      border-bottom: 1px solid rgb(235, 73, 73);
    }
  }

  &__textarea {
    max-width: 540px;
    width: 100%;
    height: 136px;

    font-weight: 300;
    font-size: 21px;
    line-height: 29px;

    color: #bdbdbd;
    border: none;
    border-bottom: 1px solid #bdbdbd;

    resize: vertical;
    background-color: transparent;
    box-sizing: border-box;
    padding: 15px 0 0;
    box-sizing: border-box;
    &:focus {
      outline: none;
      border-bottom: 1px solid #005fa3;
    }
  }
}

.invalid {
  border-bottom: 1px solid rgb(235, 73, 73);
}

@media (max-width: 1080px) {
  .ContactForm {
    &__content {
      max-width: 100%;
      width: 100%;
    }

    &__container {
      max-width: 100%;
      width: 100%;
      justify-content: space-between;
    }
    &__textarea {
      max-width: 100%;
      width: 100%;
    }
  }
}
@media (max-width: 480px) {
  .ContactForm {
    &__container {
      flex-direction: column;
    }

    &__inputs {
      margin: 0 0 35px;
    }

    &__input {
      max-width: 100%;
      width: 100%;
    }
  }
}
